;\r\n    font-size: 1.1rem;\r\n    border-radius: 0.1rem;\r\n    box-shadow: 0.1rem 0.1rem 0.2rem -0.1rem #3e3e3e;\r\n    &--expanded{\r\n      padding: 1rem 1rem;\r\n      background-color: #32353c;\r\n      margin: 1rem 0;\r\n    }\r\n  }\r\n}\r\n\r\n.filter{\r\n  @extend %inputStyle;\r\n  padding: 0.4rem 0 0.4rem 0.9rem;\r\n  font-size: 1.1rem;\r\n  background: #bac3cc;\r\n  color: #0e2e4e;\r\n}\r\n\r\n.category-selector{      background: #6f8fc338;\r\n  color: #bddde0;\r\n  padding: 1rem 1rem 0.5rem 1rem;\r\n  border-radius: 5px;\r\n  border: 1px solid #415569;\r\n\r\n  header{\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    margin-bottom: 1.5rem;\r\n  }\r\n  &__heading{    \r\n    margin-right: 2rem;\r\n    color: #b0d5fb;\r\n  }\r\n  &__filter{\r\n    flex: 1;\r\n  }\r\n\r\n  .categories-container{\r\n    overflow-y: auto;\r\n    max-height: 10rem;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    flex-wrap: wrap;\r\n    > *{\r\n      white-space: nowrap;\r\n    }\r\n    &::after{\r\n      content: '';\r\n      flex: 1;\r\n    }\r\n\r\n  }\r\n}\r\n\r\n\r\n.field {\r\n  margin-bottom: 1rem;\r\n  &__name {\r\n    text-transform: capitalize;\r\n    font-family: monospace;\r\n    font-size: 1.1rem;\r\n    margin-bottom: .5rem;\r\n    display: inline-block;\r\n    color: #80afe0;\r\n  }\r\n  &__input,\r\n  &__text-area {\r\n    @extend %inputStyle;\r\n  }\r\n  &__text-area {\r\n    height: 6rem;\r\n    &--disabled{      \r\n      color: #444;\r\n      background: #bbb;\r\n    }\r\n  }\r\n  &__checkboxes {\r\n    background: white;\r\n    color: black;\r\n    text-shadow: none;\r\n    padding: 1rem 1rem 0.5rem 1rem;\r\n    max-height: 6rem;\r\n    border-radius: 5px;\r\n    border: 1px solid #a9a9a9;\r\n    overflow-y: scroll;\r\n\r\n    label {\r\n      width: 50%;\r\n      display: inline-block;\r\n      padding-right: 1rem;\r\n      box-sizing: border-box;\r\n      margin-bottom: 0.5rem;\r\n\r\n   